.gallery__wrap
    display: flex
    flex-wrap: wrap
    justify-content: space-evenly
    // background-color: $paragraphBackgroudColor
    width:50vw
    margin:0 auto
    padding: 1.5rem
.gallery__title
    margin: 0 auto
    width:50vw
.gallery__photo
    img
        width: 10vw
        margin: 5px
        cursor: pointer
        border: 2px solid transparent
        &:hover
            // border: 2px solid $galleryBorderColor

.popup
    background-color: rgba(80, 80, 80, 0.8)
    position: fixed
    left:0
    top:0
    width: 100vw
    height: 100vh
    

.popup--image
    
    max-width: 40vw
    max-height: 80vh

.svg-inline--fa
    position: absolute
    top:50%
    color: $awesomeIconColor
    font-size: 3rem
    cursor: pointer
.popup__wrap
    
    position: absolute
    left:50%
    top:50%
    transform: translate(-50%, -50%)
.fa-angle-left
    left:25%
.fa-angle-right
    left:73%
.fa-circle-xmark
    position: absolute
    top: 10px
    right:10px
.fa-house
    position: fixed
    left:30px
    top:50px
.disabled
    color: grey