.navigation
    min-height: 10vh
    background-color: $navigationBackgroundColor

.navigation__element
    text-decoration: none
    text-transform: uppercase
    font-size: 2rem
    color:$navigationTextColor
    border-bottom: 3px solid transparent
    &:hover
         border-bottom: 3px solid $navigationTextColor
         transition: 0.3s
.navigation__wrap
    min-height: 10vh
    display: flex
    justify-content: space-evenly
    align-items: center
    width: 70vw
    margin: 0 auto
.active
    border-bottom: 3px solid $navigationTextColor
    
